import { GetCheckoutDataQuery } from '@/checkout/__generated__/types';
import {
  Address as AddressModel,
  ShippingAddress as ShippingAddressModel,
  BillingAddress as BillingAddressModel,
} from '@/checkout/data/models/address';
import {
  Region as RegionModel,
  Country as CountryModel,
  CustomAttribute as CustomAttributeModel,
} from '@/checkout/data/models';
import {
  transformSelectedShippingMethod,
  transformShippingMethods,
} from '@/checkout/data/transforms/transform-shipping-methods';

type ShippingAddresses = NonNullable<
  GetCheckoutDataQuery['cart']
>['shipping_addresses'];
type ShippingAddress = ShippingAddresses[0];
type NonNullableShippingAddress = NonNullable<ShippingAddress>;

type AddressRegion = NonNullableShippingAddress['region'];
type NonNullableAddressRegion = NonNullable<AddressRegion>;
type ValidatedAddressRegion = {
  code: NonNullable<NonNullableAddressRegion['code']>;
  label: NonNullable<NonNullableAddressRegion['label']>;
  region_id: NonNullableAddressRegion['region_id'];
};

type AddressCountry = NonNullableShippingAddress['country'];
type NonNullableAddressCountry = NonNullable<AddressCountry>;
type ValidatedAddressCountry = {
  code: NonNullable<NonNullableAddressCountry['code']>;
  label: NonNullable<NonNullableAddressCountry['label']>;
};

type BillingAddress = NonNullable<
  GetCheckoutDataQuery['cart']
>['billing_address'];
type NonNullableBillingAddress = NonNullable<BillingAddress>;

type CustomAttributes =
  | NonNullableShippingAddress['custom_attributes']
  | NonNullableBillingAddress['custom_attributes'];

const isValidAddressRegion = (region: AddressRegion): boolean => {
  if (!region) return false;
  return !!region.code && !!region.label;
};

const transformAddressRegion = (
  data: AddressRegion
): RegionModel | undefined => {
  if (!isValidAddressRegion(data)) return;
  const { code, label, region_id } = data as ValidatedAddressRegion;
  if (!region_id) return { code, name: label };
  return { code, name: label, id: region_id };
};

const transformAddressCountry = (data: AddressCountry): CountryModel => {
  const { code, label } = data as ValidatedAddressCountry;
  return { value: code, label };
};

const isValidCustomAttribute = (attribute: any): boolean => {
  if (!attribute) return false;
  return 'code' in attribute && 'value' in attribute;
};

export const transformCustomAttributes = (
  data: CustomAttributes
): CustomAttributeModel[] => {
  return data.filter(isValidCustomAttribute).map((attribute) => {
    const { code, value } = attribute as { code: string; value: string };
    return { code, value };
  });
};

const buildBaseAddress = (
  data: NonNullableShippingAddress | NonNullableBillingAddress
): AddressModel => {
  const street = data.street.filter(Boolean) as string[];

  return {
    firstName: data.firstname,
    lastName: data.lastname,
    company: data.company || undefined,
    city: data.city,
    street,
    postCode: data.postcode || undefined,
    vatId: data.vat_id || undefined,
    telephone: data.telephone || undefined,
    region: transformAddressRegion(data.region),
    country: transformAddressCountry(data.country),
    customAttributes: transformCustomAttributes(data.custom_attributes),
  };
};

const transformBillingAddress = (
  data: BillingAddress
): BillingAddressModel | undefined => {
  if (!data) return;
  return buildBaseAddress(data);
};

const transformShippingAddresses = (
  data: ShippingAddresses
): ShippingAddressModel[] | undefined => {
  return data
    .filter((address) => !!address)
    .map((address) => {
      const {
        available_shipping_methods,
        selected_shipping_method,
        ...baseData
      } = address as NonNullableShippingAddress;

      return {
        ...buildBaseAddress(baseData),
        availableShippingMethods: transformShippingMethods(
          available_shipping_methods
        ),
        selectedShippingMethod: transformSelectedShippingMethod(
          selected_shipping_method
        ),
      };
    });
};

export {
  ShippingAddress,
  BillingAddress,
  transformBillingAddress,
  transformShippingAddresses,
};
