import { GetCheckoutDataQuery } from '@/checkout/__generated__/types';
import { Cart as CartModel } from '@/checkout/data/models';
import { transformCartItemsV2 } from '@/checkout/data/transforms';
import {
  transformAvailablePaymentMethods,
  transformSelectedPaymentMethod,
} from '@/checkout/data/transforms/transform-payment-methods';

import {
  transformShippingAddresses,
  transformBillingAddress,
} from '@/checkout/data/transforms/transform-address';

type Cart = NonNullable<GetCheckoutDataQuery['cart']>;
type NonNullableCoupons = NonNullable<Cart['applied_coupons']>;
type NonNullableCoupon = NonNullable<NonNullableCoupons[number]>;

export const transformCoupons = (coupons: Cart['applied_coupons']) => {
  if (!coupons) return [];

  return coupons.filter(Boolean).map((coupon) => {
    const { code } = coupon as NonNullableCoupon;
    return { code };
  });
};

const transformCart = (cart: Cart): CartModel => {
  return {
    coupons: transformCoupons(cart.applied_coupons),
    availablePaymentMethods: transformAvailablePaymentMethods(
      cart.available_payment_methods
    ),
    billingAddress: transformBillingAddress(cart.billing_address),
    email: cart.email ?? undefined,
    id: cart.id,
    virtual: cart.is_virtual,
    items: transformCartItemsV2(cart.itemsV2),
    prices: cart.prices,
    selectedPaymentMethod: transformSelectedPaymentMethod(
      cart.selected_payment_method
    ),
    shippingAddresses: transformShippingAddresses(cart.shipping_addresses),
    totalQty: cart.total_quantity,
  };
};

export { Cart, transformCart };
