import {
  GetCheckoutDataQuery,
  ProductStockStatus,
} from '@/checkout/__generated__/types';
import { Item, ItemKind, StockStatus } from '@/checkout/data/models';

type CartItemsV2 = NonNullable<GetCheckoutDataQuery['cart']>['itemsV2'];
type NonNullableCartItemsV2 = NonNullable<CartItemsV2>;
type CartItems = NonNullableCartItemsV2['items'];
type NonNullableCartItems = NonNullable<NonNullableCartItemsV2['items']>;
type NonNullableCartItem = NonNullable<NonNullableCartItems[number]>;

function transformType(typename: string): ItemKind {
  switch (typename) {
    case 'SimpleCartItem':
      return ItemKind.Simple;
    case 'ConfigurableCartItem':
      return ItemKind.Configurable;
    case 'DownloadableCartItem':
      return ItemKind.Downloadable;
    case 'GiftCardCartItem':
      return ItemKind.GiftCard;
    case 'VirtualCartItem':
      return ItemKind.Virtual;
    case 'BundleCartItem':
      return ItemKind.Bundle;
    default:
      throw new Error(`Unknown item kind: ${typename}`);
  }
}

function transformConfigurableOptions(item: NonNullableCartItem) {
  if (item.__typename !== 'ConfigurableCartItem') return;

  const configurableOptions: Record<string, string> = {};

  for (const option of item.configurable_options) {
    configurableOptions[option!.option_label] = option!.value_label;
  }

  return configurableOptions;
}

function transformCustomizableOptions(item: NonNullableCartItem) {
  if (!('customizable_options' in item)) return;

  const customizableOptions: Record<string, string> = {};

  for (const option of item.customizable_options) {
    customizableOptions[option!.label] = option!.values
      // some customizable option types don't have a label (empty string), so we use the value instead
      .map((optionValue) => optionValue!.label || optionValue!.value)
      .join(', ');
  }

  return customizableOptions;
}

function transformImage(item: NonNullableCartItem) {
  const isConfigurable = item.__typename === 'ConfigurableCartItem';

  const thumbnail = isConfigurable
    ? item.configured_variant?.thumbnail
    : item.product?.thumbnail;

  if (!thumbnail || !thumbnail.url) return;

  const image: Item['image'] = { src: thumbnail.url };
  const alt = String(thumbnail.label || item.product.name);
  if (alt !== '') image.alt = alt;
  return image;
}

function transformCartItem(item: NonNullableCartItem) {
  return {
    kind: transformType(item.__typename),
    name: item.product.name!,
    quantity: item.quantity,
    sku: item.product.sku!,
    uid: item.uid,
    url: item.product?.url_key,
    image: transformImage(item),
    price: {
      value: item.prices!.price.value!,
      currency: item.prices!.price.currency!,
    },
    priceInclTax: {
      value: item.prices!.price_including_tax.value!,
      currency: item.prices!.price_including_tax.currency!,
    },
    total: {
      value: item.prices!.row_total.value!,
      currency: item.prices!.row_total.currency!,
    },
    totalInclTax: {
      value: item.prices!.row_total_including_tax.value!,
      currency: item.prices!.row_total_including_tax.currency!,
    },
    discount: {
      value: item.prices!.total_item_discount!.value!,
      currency: item.prices!.total_item_discount!.currency!,
    },
    regularPrice: {
      value: item.product.price_range.maximum_price!.regular_price.value!,
      currency: item.product.price_range.maximum_price!.regular_price.currency!,
    },
    discounted:
      item.product.price_range.maximum_price!.regular_price.value! *
        item.quantity !==
      item.prices!.row_total.value,
    stockStatus:
      item.product.stock_status === ProductStockStatus.OutOfStock
        ? StockStatus.OutOfStock
        : StockStatus.InStock,
    configurableOptions: transformConfigurableOptions(item),
    customizableOptions: transformCustomizableOptions(item),
    links:
      item.__typename === 'DownloadableCartItem'
        ? item.links?.map((option) => option?.title!)
        : undefined,
    onlyXLeftInStock: item.product.only_x_left_in_stock,
    senderName:
      item.__typename === 'GiftCardCartItem' ? item.sender_name : undefined,
    senderEmail:
      item.__typename === 'GiftCardCartItem' ? item.sender_email! : undefined,
    recipientEmail:
      item.__typename === 'GiftCardCartItem'
        ? item.recipient_email!
        : undefined,
    recipientName:
      item.__typename === 'GiftCardCartItem' ? item.recipient_name : undefined,
    message: item.__typename === 'GiftCardCartItem' ? item.message : undefined,
  };
}

export function transformCartItems(items: CartItems): Item[] {
  return items
    .filter(Boolean)
    .map((item) => transformCartItem(item as NonNullableCartItem));
}

export function transformCartItemsV2(itemsV2: CartItemsV2): Item[] {
  if (!itemsV2) return [];

  return transformCartItems(itemsV2.items);
}
